import React, { useState } from 'react';
import "../../utils/Orange.css"

function ExpandableTable() {
  const [expandedRows, setExpandedRows] = useState({});

  const toggleExpand = (id) => {
    setExpandedRows((prevState) => ({
      ...prevState,
      [id]: !prevState[id]
    }));
  };

  return (
    <table className="table">
      <thead>
      <tr>
        <th>ID</th>
        <th>Name</th>
      </tr>
      </thead>

      <tbody>
      <tr>
        <td className="expandable" onClick={() => toggleExpand(1)}>1</td>
        <td>John</td>
      </tr>
      <tr id="expandedRow1" className={expandedRows[1] ? "" : "expanded-row-hidden"}>
        <td colSpan="2">
          Additional content for ID 1.
        </td>
      </tr>

      <tr>
        <td className="expandable" onClick={() => toggleExpand(2)}>2</td>
        <td>Doe</td>
      </tr>
      <tr id="expandedRow2" className={expandedRows[2] ? "" : "expanded-row-hidden"}>
        <td colSpan="2">
          Additional content for ID 2.
        </td>
      </tr>

      <tr>
        <td className="expandable" onClick={() => toggleExpand(3)}>3</td>
        <td>Smith</td>
      </tr>
      <tr id="expandedRow3" className={expandedRows[3] ? "" : "expanded-row-hidden"}>
        <td colSpan="2">
          Additional content for ID 3.
        </td>
      </tr>
      </tbody>
    </table>
  );
}

export default ExpandableTable;
